import {Component, Inject, OnInit} from '@angular/core';
import {FormControl, FormGroup, Validators} from '@angular/forms';

import {Contact} from '../../../shared/interfaces';
import {ContactsService} from '../../shared/services/contacts.service';
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material/dialog";
import {Router} from "@angular/router";

@Component({
  selector: 'app-contact-action',
  templateUrl: './contact-action.component.html',
  styleUrls: ['./contact-action.component.css']
})
export class ContactActionComponent implements OnInit {

  contact: Contact;
  form: FormGroup;

  submitValue: string;
  submitHandler: Function;

  constructor(
    private contactsService: ContactsService,
    private router: Router,

    private thisDialog: MatDialogRef<ContactActionComponent>,
    @Inject(MAT_DIALOG_DATA) public data: object
  ) {
  }

  ngOnInit(): void {
    this.initForm();

    if (this.data['action'] === 'create') {
      this.setCreateFormValues();
    }
    if (this.data['action'] === 'edit') {
      this.setEditFormValues();
    }
  }

  private initForm() {
    this.form = new FormGroup({
      'name': new FormControl(null, Validators.required),
      'phone': new FormControl(null, Validators.required)
    });
  }

  private setCreateFormValues() {
    this.submitValue = 'Create';
    this.submitHandler = () => this.create();
  }

  private setEditFormValues() {
    this.contact = Object.assign({}, this.data['contact']);

    this.form.get('name').setValue(this.contact.name);
    this.form.get('phone').setValue(this.contact.phone_number);

    this.submitValue = 'Edit';
    this.submitHandler = () => this.edit();
  }

  edit() {
    this.contact.name = this.form.value.name;
    this.contact.phone_number = this.form.value.phone;
    this.contactsService.update(this.contact);
    this.thisDialog.close(true);
    this.router.navigate([], { replaceUrl: true});
  }

  create() {
    this.contact = {
      name: this.form.value.name,
      phone_number: this.form.value.phone
    };

    this.contactsService.add(this.contact);
    this.thisDialog.close(true);
    this.router.navigate([], { replaceUrl: true});
  }
}
