import {Component, OnInit} from '@angular/core';

import {ContactsService} from '../../shared/services/contacts.service';
import {Contact} from '../../../shared/interfaces';
import {DialogOverviewService} from "../../shared/services/dialog-overview.service";
import {SearchOptions} from "../../shared/components/search/search.component";
import {ContactActionComponent} from "../contact-action/contact-action.component";

@Component({
  selector: 'app-contacts',
  templateUrl: './contacts-page.component.html',
  styleUrls: ['./contacts-page.component.css']
})
export class ContactsPageComponent implements OnInit {

  searchOptions: SearchOptions;
  searchedContacts: Contact[] = [];
  searchNow: boolean = false;
  noSearchedContacts: boolean = true;

  constructor(
    public contactsService: ContactsService,
    public dialogOverview: DialogOverviewService
  ) {

  }

  ngOnInit(): void {
    this.searchedContacts = [];
    this.searchOptions = {
      uniqueAttribute: 'id',
      selector: '.contact',
      subSelectors: [
        {name: 'name', selector: '.name', isActive: false},
        {name: 'phone', selector: '.phone', isActive: false},
      ]
    };
  }

  onSearch(event) {
    if (event["value"] === '') {
      this.searchedContacts = [];
      this.noSearchedContacts = true;
      this.searchNow = false;
      return;
    }
    this.contactsService.search(event["value"], event["subSelectors"])
      .subscribe((contacts: Contact[]) => {
        this.searchedContacts = contacts;
        this.noSearchedContacts = !this.searchedContacts.length;
        this.searchNow = true;
      });
  }

  onExport() {
    if (this.searchedContacts.length) {
      this.contactsService.export(this.searchedContacts);
    } else {
      this.contactsService.export();
    }
  }

  onImport() {
    this.contactsService.import();
  }

  onAddContact() {
    this.dialogOverview.openDialog(ContactActionComponent, {
      action: 'create',
    });
  }
}
