import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {ReactiveFormsModule} from '@angular/forms';
import {RouterModule} from '@angular/router';

import {ContactActionComponent} from './contact-action/contact-action.component';
import {ContactCardComponent} from './contact-card/contact-card.component';
import {ContactsPageComponent} from './contacts-page/contacts-page.component';

import {ProfileSharedModule} from '../shared/profile-shared.module';
import {DialogOverviewService} from "../shared/services/dialog-overview.service";

@NgModule({
  declarations: [
    ContactActionComponent,
    ContactCardComponent,
    ContactsPageComponent
  ],
  imports: [
    CommonModule,
    ProfileSharedModule,
    ReactiveFormsModule,

    RouterModule.forChild([
      {path: '', component: ContactsPageComponent},
    ]),
    ProfileSharedModule,
  ],

  entryComponents: [
    ContactActionComponent
  ],

  providers: [
    DialogOverviewService
  ]

})
export class ContactsModule {

}
