import {AfterViewInit, Component, ElementRef, Input} from '@angular/core';
import {ActivatedRoute, Params} from '@angular/router';

import {Contact} from '../../../shared/interfaces';
import {ContactsService} from '../../shared/services/contacts.service';
import {ContactActionComponent} from "../contact-action/contact-action.component";
import {DialogOverviewService} from "../../shared/services/dialog-overview.service";
import {TasksService} from "../../shared/services/tasks.service";

@Component({
  selector: 'app-contact-card',
  templateUrl: './contact-card.component.html',
  styleUrls: ['./contact-card.component.css']
})
export class ContactCardComponent implements AfterViewInit {

  @Input() contact: Contact;
  isSelected: boolean;

  constructor(
    private contactsService: ContactsService,
    private tasksService: TasksService,
    private dialogOverview: DialogOverviewService,
    private route: ActivatedRoute,
    private el: ElementRef
  ) {
  }

  ngAfterViewInit(): void {
    this.route.queryParams.subscribe((params: Params) => {
      if (+params["select"] === this.contact.id) {
        this.isSelected = true;
        this.el.nativeElement.scrollIntoView({behavior: 'smooth'});
      }
    })
  }

  onEdit() {
    this.dialogOverview.openDialog(ContactActionComponent, {
      action: 'edit',
      contact: {...this.contact}
    });
  }

  onDelete() {
    this.contactsService.delete(this.contact);
    this.tasksService.tasks.forEach(
      task => task.contacts = task.contacts.filter(ct => ct.id !== this.contact.id)
    );
  }

  onCopy() {
    let clone: Contact = Object.assign({}, this.contact);
    clone.id = null;
    this.contactsService.add(clone);
  }

  hideSelect() {
    this.isSelected = false;
  }
}
